@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200&display=swap);

.App_app__3OoO3 {
  text-align: center;
  font-family: 'Montserrat', 'Courier New', sans-serif;
}
.App_app__3OoO3 button {
  background-color: darkorange;
  height: 40px;
  color: rgba(255,255,255,0.8);
  font-weight: bold;
  border-top: 2px solid rgba(244,179,80,1);
  border-left: 2px solid rgba(244,179,80,1);
  border-right: 2px solid rgba(232,126,4,1);
  border-bottom: 2px solid rgba(232,126,4,1);
}
.App_app__3OoO3 button:hover {
  cursor: pointer;
  color: #fff;
  background-color: rgba(232,126,4,1);
}
.App_app__3OoO3 button:active {
  cursor: pointer;
  color: #fff;
  border-top: 2px solid rgba(232,126,4,1);
  border-left: 2px solid rgba(232,126,4,1);
  border-right: 2px solid rgba(244,179,80,1);
  border-bottom: 2px solid rgba(244,179,80,1);
}
.App_header__2g8RA {
  color: rebeccapurple;
  border-bottom: 1px solid lightgrey;
  margin-bottom: 30px;
  font-weight: bolder;
}
.App_recipesList__354cF {
  width: 90%;
  margin: 30px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: space-between;
  grid-gap: 40px;
}

/*modal window*/
.App_modal__xbnQ9 {
  position: fixed;
  top:0;bottom:0;left:0;right:0;
  background-color: rgba(228,233,237,0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
}
.App_modalCont__GWO_5 {
  width: 700px;
  min-height: 500px;
  max-height: 100vh;
  overflow-y: auto;
  background-color: #fff;
  outline: 2px solid lightgrey;
  position: relative;
  display: flex;
  flex-direction: row;
}
.App_modalDescriptionCont__1y7Qi {
  display: flex;
  flex-direction: column;
}
.App_modalDescriptionCont__1y7Qi> :last-child {
  margin-top: auto;
}
.App_modalTitle__l-VQj {
  padding: 25px;
  color: darkorange;
  font-weight: bold;
  font-size: x-large;
}
.App_modalDescription__3e6_5 {
  padding: 0 25px;
  font-size: large;
}
.App_modalCont__GWO_5 img {
  height: 100%;
  width: 350px;
  object-fit: cover;
}

/*close icon*/
.App_close__2vdq1 {
  position: absolute;
  right: 20px;
  top: 10px;
  width: 20px;
  height: 20px;
  opacity: 0.3;

}
.App_close__2vdq1:hover {
  opacity: 1;
  cursor: pointer;
}
.App_close__2vdq1:before, .App_close__2vdq1:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: #333;
}
.App_close__2vdq1:before {
  transform: rotate(45deg);
}
.App_close__2vdq1:after {
  transform: rotate(-45deg);
}

/*cart*/
.App_cart__3oQ1u {
  position: fixed;
  right: 5px;
  bottom: 5px;
  background-color: rgba(255,255,255,1);
  border: 1px solid grey;
  height: 70px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.App_cartCount__3ZJUl {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.App_cartCount__3ZJUl> :first-child {
  line-height: 50px;
  font-weight: bolder;
}
.App_cartCount__3ZJUl img {
  height: 50px;
}

/*responsive*/
@media screen and (max-width: 800px) {
  .App_recipesList__354cF {width: 100%; grid-template-columns: 1fr 1fr}
  .App_modal__xbnQ9{height: 100vh; background-color: #fff }
  .App_modalCont__GWO_5 {flex-direction: column; height: 100vh; width: 100%;outline: none}
  .App_modalCont__GWO_5 img {width: 100%}
  .App_modal__xbnQ9 button {position: fixed;bottom: 0; width: 100%}
  .App_modalDescription__3e6_5{padding-bottom: 40px}
  .App_recipesList__354cF {grid-gap: 20px}
}
@media screen and (max-width: 450px) {
  .App_recipesList__354cF {grid-template-columns: 1fr}
  .App_cart__3oQ1u {width: 100%; right: -1px;bottom: 0}
}



